.node {
	border-width  : medium;
	border-style  : solid;
	border-radius : 5px;

	background-color: white;
	color:black;
	z-index:2;

	left:0px;
	top:0px;

	position:absolute;

	display:table;
}

.node-title {
	border-color  : black;
	border-width  : medium;
	border-bottom : solid;
	border-radius : 5px 5px 0 0;

	background-color: white;
}

.node-body {
	border-color  : black;
	border-width  : medium;
	border-radius : 5px 5px 0 0;

	background-color: white;
}

.node-body p {
	margin  : 0px;
	padding : 0px;
}


.edge {
	position:absolute;
	z-index:1;
	stroke:rgb(255,255,0);
	stroke-width:10;
}

#lists {
	position:absolute;
	left:0;
	top:0;
	width:0px;
	height: 0px;
}

#canv {
	position:fixed;
	width: 100%;
	height: 100%;
	z-index:1;
}

.todoItem {
	float:left;
	text-align: left;
	overflow-wrap: break-word;
}

.todoRemove {
	float:right;
}

ul {
	margin: 5px 0px;
	padding-left: 20px;
}

li {
	margin: 0px 5px;
}

.controls {
	position:absolute;
	left:10px;
	top:10px;
}

.controls input {
	position:relative;


	width:100px;

	z-index:2000;

	display:block;
}

